import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { useParams, Link } from 'react-router-dom'
import { Card, Image, Label } from 'semantic-ui-react'
// import picture from '../other pictures/checklist.svg'
import arrow from '../Other pictures/left1.svg'

const UserDetailview = () => {

  const [user, setuser] = useState()

  const param = useParams()

  useEffect(() => {
    if (!user) {
      getData(param.id)
      console.log('user', user)
    }
  }, [])


  const getData = async (id) => {
    try {
      const { data } = await axios.get(`/api/users/${id}`)
      setuser(data)
    } catch (err) {
      window.alert('Oops, data could not be retrieved!')
    }
  }

  if (!user) {
    return null
  }

  return (
    <>
      <section className="userdetail-page">

        <Link to="/userlist">

          <div className="arrow">
            <img src={arrow} className="left-arrow"></img>
          </div>
        </Link>


        <Card>
          <Image src={user.profile_image} wrapped ui={false} />
          <Card.Content>
            <Card.Header>Matthew</Card.Header>
            {/* <Card.Meta>
              <span className='date'>Joined in 2015</span>
            </Card.Meta> */}
            <Card.Description>
              {user && user.interests.map((interest, i) => {
                return (<Label as="a" color="teal" tag key={i} className="userdetail-label">
                  {interest.label}
                </Label>)
              })}
            </Card.Description>
          </Card.Content>
          {/* <Card.Content extra>
            <a>
              <Icon name='user' />
              22 Friends
            </a>
          </Card.Content> */}
        </Card>

      </section>






    </>
  )
}

export default UserDetailview