import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Form } from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import social from '../Other pictures/social.svg'
import axios from 'axios'

const Register = () => {

  const history = useHistory()
  const [registerData, setRegisterData] = useState({
    username: '',
    email: '',
    password: '',
    password_confirmation: '',
    profile_image: '',
    interests: [],
    age: '',
    preferences: 'WO',
  })

  // const preference = ['Men', 'Women', 'Bisexual']

  const [interests, setInterests] = useState([])

  const [errors, setErrors] = useState('')

  useEffect(() => {
    if (!interests.length) {
      fetchInterests()
    }
  }, [])


  const fetchInterests = async () => {
    try {
      const req = await axios.get('api/interests')
      setInterests(req.data)
    } catch (err) {
      console.log(err)
    }
  }

  //handling data that comes through registration
  const handleRegisterData = (event) => {
    const getUserData = { ...registerData, [event.target.name]: event.target.value }
    const newError = { ...errors, [event.target.name]: '' }
    setRegisterData(getUserData)
    setErrors(newError)
  }


  const submitForm = async event => {
    event.preventDefault()
    console.log(event)
    try {
      // await axios.post('/api/auth/register/', registerData)
      const req = await axios.post('/api/auth/register/', registerData)
      window.localStorage.setItem('token', req.data.token)
      history.push('/')
      // console.log(req)
    } catch (err) {
      console.log(err)
    }
  }

  const selectInterests = (id) => {
    if (registerData.interests.includes(id)) {
      const newInterests = registerData.interests
      newInterests.splice(registerData.interests.indexOf(id), 1)
      // console.log(newInterests)
      setRegisterData({
        ...registerData,
        interests: [...newInterests],
      })
    } else {
      setRegisterData({
        ...registerData,
        interests: [...registerData.interests, id],
      })
    }
  }



  console.log('interests', registerData.interests)
  console.log(registerData)

  return (
    <div className="register-page">

      <div className="register-phone">
        <img src={social}></img>
      </div>

      <div className="register-form">

        <Form className="form-registration" onSubmit={submitForm}>
          <Form.Field>
            <input
              className="register-input"
              type="text"
              placeholder="Username"
              name="username"
              value={registerData.username}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <Form.Field>
            <input
              className="register-input"
              type="text"
              placeholder="Profile image"
              name="profile_image"
              value={registerData.profile_image}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <Form.Field>
            <input
              className="register-input"
              type="number"
              placeholder="Age"
              name="age"
              value={registerData.age}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <Form.Field>
            <input
              className="register-input"
              type="text"
              placeholder="Email"
              name="email"
              value={registerData.email}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <Form.Field>
            <input
              className="register-input"
              type="password"
              placeholder="Password"
              name="password"
              value={registerData.password}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <Form.Field>
            <input
              className="register-input"
              type="password"
              placeholder="Password confirmation"
              name="password_confirmation"
              value={registerData.password_confirmation}
              required
              onChange={handleRegisterData} />
          </Form.Field>

          <div className="your-interests">What are your interests?</div>
          <div className="interest-container">{interests && interests.map(
            (item, key) => {
              return (<span className={`${registerData.interests.includes(item.id) ? 'active' : ''} interest`} key={key} onClick={() => selectInterests(item.id)}>{item.label}</span>)
            })}
          </div>

          <button className="button-register" >Join us</button>

          {/* </Link> */}
          <p className="log-in">Have an account already? <Link to="/login">Log in</Link></p>

          <Link to="/userlist"><div>Click here</div></Link>
        </Form>


      </div>
    </div>
  )
}
export default Register