import React, { useState } from 'react'
import { Button, Form } from 'semantic-ui-react'
import { Link, useHistory } from 'react-router-dom'

import phone from '../Other pictures/phone.svg'
import user from '../Other pictures/man.svg'
import axios from 'axios'
import { setToken } from './auth'


const Login = () => {

  const history = useHistory()

  const [dataFromLogin, setDataFromLogin] = useState({
    email: '',
    password: '',
  })


  const handleLoginData = (event) => {
    const newLoginData = { ...dataFromLogin, [event.target.name]: event.target.value }
    // console.log(newLoginData)
    setDataFromLogin(newLoginData)
  }

  const submitLogin = async event => {
    event.preventDefault()
    try {
      const { data } = await axios.post('/api/auth/login/', dataFromLogin)
      setToken(data.token)
      history.push('/userlist')

    } catch (err) {
      window.alert('Your email or password is wrong.')
    }
  }

  console.log('login state', dataFromLogin)

  return (
    <>
      <div className="some-container">
        <div className="img-login">
          <img src={phone}></img>
        </div>
      </div>
      <div className="login-container">

        <Form className="login-form" onSubmit={submitLogin} >
          <img className="user-image" src={user}></img>
          <h2>Welcome</h2>

          <Form.Field>
            <input className="login-forms"
              type="text"
              placeholder="Email"
              name="email"
              value={dataFromLogin.email}
              onChange={handleLoginData}
              required
            />
          </Form.Field>

          <Form.Field>
            <input className="login-forms"
              type="password"
              placeholder="Password"
              name="password"
              value={dataFromLogin.password}
              onChange={handleLoginData}
              required
            />
          </Form.Field>
          <Button className="login-button" type="submit" onSubmit={submitLogin} >
            Log in
          </Button>

          <p>Don&apos;t have an account yet? <Link to="/register">Join us</Link></p>
        </Form>
      </div>
    </>
  )
}
export default Login