import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Card, Image } from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import { getCurrentUser } from './auth'
import { findCommonInterests } from './utils'


const Userlist = () => {

  const [users, setUsers] = useState([])
  const [hasError, setHasError] = useState(false)

  useEffect(() => {
    getAuthUser()
  }, [])

  //this returns the filtered list of users based on the current user's interests 
  const getData = async (query) => {
    try {
      // returns a full list of users
      const { data } = await axios.get('/api/users')
      // filtering users that have same interests in common with the current logged in user
      // TODO: move logic to backend users/views.py
      const filteredUsers = data.filter(user => {
        const userInterestsIds = user.interests.map(interest => interest.id)
        // fincCommonInterests -> iterates through a couple of arrays and finds common interests
        return findCommonInterests(userInterestsIds, query)
      })
    
      console.log('query >>>', query)
      setUsers(filteredUsers)
    } catch (err) {
      setHasError(true)
    }
  }

  // this functions get the currently logged in user
  const getAuthUser = async () => {
    try {
      // using the auth method, we get the current user
      const user = await getCurrentUser()
      // we are storing a list of the currently logged in user's interests id
      const query = await user.interests.map(interest => interest.id)
      // console.log('query of interests >>>', query)
      getData(query)
    } catch (err) {
      console.log(err)
    }
  }

  // console.log('what are user doing', users)

  if (!users.length) {
    return (<h2 className="title has-text-centered">
      {hasError ? 'Something has gone wrong!' : 'loading...'}
    </h2>)
  }
  return (
    <>
      <div className="userlist-page">
        <div className="container userlist">
          {users.map(item => {
            return (
              <Link to={`userdetail/${item.id}`} key={item.id}>


                <Card >
                  <Image className="image-userlist" src={item.profile_image} />
                  <Card.Content>
                    <Card.Header>{item.username}</Card.Header>
                  </Card.Content>
                </Card>
              </Link>
            )
          })}
        </div>
      </div>
    </>
  )
}
export default Userlist